import Layout from '../../components/layout';
import bell from "../../assets/results/bell.webp"
import Sidebar from '../../components/results/sidebar';
import Report from '../../components/results/report';
import ResultForm from '../../components/results/form';
import style from './style.css';

const Results = () => {
	return (
		<Layout>
		<div class={style.results}>
            <div class={style.container}>
               <div class={style.pageTitle}>
                    <h2>Your results <img src={bell} alt="bell" /></h2>
                    <p>Your profile analysis is complete! Please find your results below.</p>
                </div>
                <div class={style.resultGrid}>
                    <Sidebar />
                    <Report />
                </div>
                <div class={style.callToForm}>
                    <ResultForm />
                </div>
            </div>
		</div>
		</Layout>
	);
};

export default Results;
