import { Link } from 'preact-router/match';
import style from './style.css'

const Report = () => (
	<div class={style.report}>
	    <div class={style.reportGrid}>
            <p>ELIGIBLE IMMIGRATION PROGRAMS</p>
            <div class={style.reportBox}>
               <ReportCard />
               <ReportCard />
               <ReportCard />
               <ReportCard />
            </div>
			
			
		</div>
	</div>
);

export default Report;


const ReportCard = () => (
	<div class={style.reportCard}>
	    <div class={style.reportCardTitle}>
            <h4>Express Entry</h4>
            <span>Excellent chances of being accepted!</span>
            <div class={style.reportLoader} />
            <p>PNP programs offer a pathway to permanent residence for skilled workers who are interested in immigrating to one of 13 Canadian provinces or territories.</p>
		</div>
        <div class={style.reportCardFooter}>
            <p>A deeper profile research is required. Please contact an immigration professional fom more information.</p>
            <Link href="/">Learn More</Link>
        </div>
	</div>
);