import { Link } from "preact-router/match";
import check from "../../assets/results/check.webp"
import style from './style.css'

const Sidebar = () => (
	<div class={style.sidebar}>
	    <div class={style.sidebarGrid}>
            <p>Profile Summary</p>
            <ul>
                <li>
                    <span>Age <img src={check} alt="check" /></span>
                    <b>18 <img src={check} alt="check" /></b>
                </li>
                <li>
                    <span>Education <img src={check} alt="check" /></span>
                    <b>High school diploma <img src={check} alt="check" /></b>
                </li>
                <li>
                    <span>Work experience (Canada) <img src={check} alt="check" /></span>
                    <b>2 years <img src={check} alt="check" /></b>
                </li>
                <li>
                    <span>Work experience (World) <img src={check} alt="check" /></span>
                    <b>I have relatives in Canada with Canadian citizenship or permanent residency <img src={check} alt="check" /></b>
                </li>
                <li>
                    <span>Job offer <img src={check} alt="check" /></span>
                    <b>I have a valid job offer from a registered Canadian employer <img src={check} alt="check" /></b>
                </li>
                <li>
                    <span>English language skills <img src={check} alt="check" /></span>
                    <b>CLB 6 - 7 (High) <img src={check} alt="check" /></b>
                </li>
                <li>
                    <span>French language skills <img src={check} alt="check" /></span>
                    <b>NCLC 0 - 3 (Low) <img src={check} alt="check" /></b>
                </li>
                <li>
                    <span>Canadian relatives <img src={check} alt="check" /></span>
                    <b>I have relatives in Canada with Canadian citizenship or permanent residency <img src={check} alt="check" /></b>
                </li>
            </ul>
			<div class={style.resubmit}>
                <Link href="/">Resubmit</Link>
            </div>
			
		</div>
	</div>
);

export default Sidebar;
